import { Component, Directive, OnInit } from '@angular/core';
import { PaymentCardControlsBase } from '@backbase/cards-common-ang';
import { BbTemplate } from '@backbase/foundation-ang/core';

@Component({
  selector: 'bb-card-details-payment-card-controls',
  templateUrl: './payment-card-controls.component.html',
})
export class PaymentCardControlsComponent extends PaymentCardControlsBase implements OnInit {
  get lockStatus() {
    return this.isLocked || false;
  }

  set lockStatus(value: boolean) {
    if (value) {
      this.onLock();
    } else {
      this.onUnlock();
    }
  }
}

@Directive({
  selector: '[bbCardDetailsPaymentCardControlsCustomizable]',
})
export class CardDetailsPaymentCardControlsCustomizableDirective extends BbTemplate<
  PaymentCardControlsComponent,
  undefined
> {}
