import { Component, Input } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { combineLatest, Observable } from 'rxjs';
import { map, pluck } from 'rxjs/operators';

@Component({
  selector: 'bb-card-details-widget',
  templateUrl: './card-details-widget.component.html',
})
export class CardDetailsWidgetComponent {
  /**
   * Notification timeout timer value configuration
   */
  @Input() notificationTtl?: number;

  /**
   * Enable to send multiple limits to the API when updating values
   */
  @Input() updateAllLimits?: boolean;

  readonly paymentCardId = this.getRouteParam(this.route, 'selectedCard').pipe(
    map((selectedCard) => selectedCard as string),
  );

  private getRouteParam(route: ActivatedRoute, param: string): Observable<string | null> {
    const paramValue = route.paramMap.pipe(pluck<ParamMap, string | null>('params', param));

    if (!route.parent) {
      return paramValue;
    }

    // Ensure that level that has actual value keeps it from being cleared.
    return combineLatest(paramValue, this.getRouteParam(route.parent, param)).pipe(
      map((params) => params[0] || params[1]),
    );
  }
  constructor(private readonly route: ActivatedRoute) {}
}
