import { HttpErrorResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, Input, OnInit, Optional } from '@angular/core';
import { CardsBackendError, CardService, filterInputParameters, PaymentCard } from '@backbase/cards-common-ang';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { WidgetPropertiesService } from '../../services/widget-properties.service';

@Component({
  selector: 'bb-card-details-container',
  templateUrl: './card-details-container.component.html',
  providers: [CardService, WidgetPropertiesService],
})
export class CardDetailsContainerComponent implements OnInit {
  private readonly notificationTtlSubject = new BehaviorSubject<number | undefined>(undefined);
  private readonly updateAllLimitsSubject = new BehaviorSubject<boolean | undefined>(undefined);

  /**
   * Observable of the payment card ID this component should retrieve the card details for.
   */
  @Input() paymentCardId: Observable<string> | undefined;
  /**
   * Notification timeout timer value configuration
   */
  // eslint-disable-next-line
  @Input('notificationTtl')
  set notificationTtlValue(value: number | undefined) {
    this.notificationTtlSubject.next(value);
  }
  /**
   * Enable to send multiple limits to the API when updating values
   */
  @Input('updateAllLimits') set updateAllLimitsValue(value: boolean | undefined) {
    this.updateAllLimitsSubject.next(value);
  }
  /**
   * Observable of the payment card
   */
  paymentCard: Observable<PaymentCard | undefined> | undefined;
  /**
   * Information about the backend error, if it occurs.
   */
  errors: CardsBackendError[] | undefined;

  private readonly handleError = (errorResponse: HttpErrorResponse[]) => {
    this.errors = errorResponse;

    return of(undefined);
  };

  ngOnInit() {
    if (typeof this.paymentCardId === 'undefined') {
      throw new Error(`"paymentCardId" input is required in "${this.constructor.name}"`);
    }

    this.cardService.getPaymentCardFrom(this.paymentCardId);

    this.paymentCard = this.cardService.getPaymentCard().pipe(
      tap(() => {
        this.cd.markForCheck();
      }),
      catchError(this.handleError),
    );
  }

  get notificationTtl(): Observable<number> {
    return filterInputParameters(this.notificationTtlSubject, this.widgetPropertiesService.notificationTtl);
  }

  get updateAllLimits(): Observable<boolean> {
    return filterInputParameters(this.updateAllLimitsSubject, this.widgetPropertiesService.updateAllLimits);
  }

  constructor(
    private readonly cardService: CardService,
    private readonly cd: ChangeDetectorRef,
    @Optional() private readonly widgetPropertiesService: WidgetPropertiesService,
  ) {}
}
